import React from 'react';
import bridge from '@vkontakte/vk-bridge';
import { platform, ANDROID, View, ModalRoot, ModalPage, ModalPageHeader, PanelHeaderButton } from '@vkontakte/vkui';
import * as Typograf from 'typograf';
import { air_schema } from 'lean-air';
import splash from './img/splash.GIF';
import '@vkontakte/vkui/dist/vkui.css';
import "./main.css";
import Rubric from './components/Rubric/Rubric';
import Profile from './components/Profile/Profile';
import LessonCard from './components/LessonCard/LessonCard';
import { iModalData, iUser, iRubric, iAchieve } from './interfaces';
import ModalCardComponent from './components/ModalCardComponent';
import { parseQueryString } from './Helpers';
import Airtable from './Airtable'
import { AIR_CONFIG } from './config'
import Api from './Api'
import { iGoodsListOption } from './Api'


let base = new Airtable(AIR_CONFIG)
let api = new Api({ airtable_conf: AIR_CONFIG })

const tp = new Typograf({ locale: ['ru', 'en-US'] });

const splashLoader = <div style={{ width: '100%', height: '100%', backgroundColor: '#770EFD' }}><img src={splash} style={{ width: '100%', height: '100%' }} alt="loading..." /></div>;

const osname = platform();


interface iAppState {
	user: iUser
	activeView: string
	activeModal: string
	modalData: iModalData
	authToken: string
	isLoading: boolean
	history: any[]
	rubrics: iRubric[]
	meta: any
	snackbar: any
	purchases: any[]
	achieves: any[]
	events: any[]
}

class App extends React.Component<any, iAppState> {

	_isMounted: boolean

	constructor(props) {
		super(props);

		this.state = {
			user: null,
			activeView: null,
			activeModal: null,
			modalData: null,
			authToken: null,
			isLoading: false,
			history: [],
			rubrics: [],
			meta: {},
			snackbar: null,
			purchases: null,
			achieves: null,
			events: null
		};

		this.setActiveModal = this.setActiveModal.bind(this);
		this._isMounted = false
	}



	setActiveModal(modal: { type: string, data?: iModalData }) {
		let activeModal = modal ? modal.type : null
		let modalData = modal ? modal.data : null
		this.setState({
			activeModal,
			modalData
		})
	}



	async componentDidMount() {



		this.setState({ isLoading: true })

		bridge.subscribe(async (e: any) => {
			switch (e.detail.type) {
				case 'VKWebAppGetUserInfoResult':
					await this.fetchData(e).catch(console.error)
					const hashParams = parseQueryString(window.location.hash);

					if (hashParams.r === 'lesson' && hashParams.id)
						this.setState({ activeView: 'lesson', meta: { lessonID: hashParams.id }, isLoading: false })
					else
						this.setState({ activeView: 'profile', isLoading: false })
					break;
				case 'VKWebAppAccessTokenReceived':
					this.setState({ authToken: e.detail.data.access_token });
					break;
				case 'VKWebAppViewRestore':
					break;
				case 'VKWebAppAllowNotificationsResult':
					break;
				default:

			}
		});

		(osname === ANDROID) ? bridge.send("VKWebAppSetViewSettings", { "status_bar_style": "light", "action_bar_color": "#000000" }) : bridge.send("VKWebAppSetViewSettings", { "status_bar_style": "light", });
		bridge.send('VKWebAppGetUserInfo', {});

	}


	async fetchData(e) {
		this._isMounted = true;
		let userData = await this.fetchUserData(e.detail.data);
		if (!userData) return

		let rubrics = await this.fetchRubricsData()
		let history = await this.fetchHistoryData(e.detail.data.id)
		let purchases = await this.fetchUserPurchases(userData[air_schema.f_users.vk_id]);
		let achieves = await this.fetchAchieves() as any[];
		let events = await this.fetchEventsByUser(userData)


		await api.levels.list({
			filterByFormula: `OR({Number}=${userData['Уровень'] + 1}, {Number}=${userData['Уровень']})`
		}).then(res => {
			userData.next_level = res.find(el => el.Number === userData['Уровень'] + 1)
			userData.current_level = res.find(el => el.Number === userData['Уровень'])
			let need_level_exp = userData.next_level["Требует опыта"] - userData.current_level['Требует опыта']
			let user_level_exp = userData['Опыт'] - userData.current_level['Требует опыта']
			userData.need_exp_to_level_up = need_level_exp;
			userData.levelExperience = user_level_exp
			return
		})

		if (this._isMounted) {
			this.setState({
				events: events,
				rubrics: rubrics,
				history: history,
				purchases: purchases,
				user: Object.assign(e.detail.data, userData),
				achieves: achieves
			})
		}

	}


	componentWillUnmount() {
		this._isMounted = false;
	}


	fetchUserData = (user: iUser): Promise<any> => api.users.initUser({
		vk_id: user.id, name: `${user.first_name} ${user.last_name}`,
	})




	async fetchUserPurchases(userID: number): Promise<any> {
		return base.list(air_schema.t_purchases, { filterByFormula: `AND({${air_schema.f_purchases.vk_id}}=${userID}, NOT({${air_schema.f_purchases.status}}="Архив"))` })
	}



	async fetchRubricsData(): Promise<iRubric[]> {
		let fields = new iRubric();

		let rubrics = await base.list('Рубрики', {
			filterByFormula: '{Опубликовано} = TRUE()',
			fields: Object.keys(fields).filter(key => fields[key] !== undefined)

		}).catch(e => []) as iRubric[]

		return rubrics.map((obj: iRubric) => {
			if (obj['Описание']) {
				obj['Описание'] = tp.execute(obj['Описание']);
				obj.desc = obj['Описание'].replace(/[*#|]/gs, '').slice(0, 60)
				if (obj.desc.length < obj['Описание'].length) obj.desc = obj.desc + "…"
			}

			return obj
		}) as iRubric[];

	}


	fetchAchieves() {
		let fields = new iAchieve();
		return base.list('Ачивки', {
			sort: [{ field: 'Кол-во работ', direction: 'asc' }, { field: 'Сложность', direction: 'asc' }],
			filterByFormula: 'NOT({Опубликовано}=BLANK())',
			fields: Object.keys(fields).filter(key => fields[key] !== undefined)
		}).catch(e => [])
	}

	async fetchHistoryData(user_id: any) {
		return api.accruals.list({
			'last_month': true,
			max_records: 10,
			vk_id: user_id,
		})
	}

	async fetchEventsByUser(user: iUser) {
		let filter = user.visited_events.map(el => `NOT({recID}="${el}")`).join(',')
		return api.events.list({
			filterByFormula: `AND(${filter})`,
			view: 'На главной в Линии',
			maxRecords: 5
		}, true)

	}

	setLocation = (route: string) => {
		if (route !== 'profile') {
			bridge.send('VKWebAppSetLocation', { location: route });
		} else {
			bridge.send('VKWebAppSetLocation', { location: '' });
		}
	}


	go = (route: string, meta?: any) => {
		if (meta) this.setState({ meta: meta })
		this.setState({ activeView: route })
		return
	};

	onRubricCellClickHandler = (route: string, meta: any) => {
		this.setState({ activeView: route })
		this.setState({ meta: meta })
	}

	fetchLessons = async () =>  api.events.list({ view: 'Ближайшие', filterByFormula: `NOT({Дата}=BLANK())` }, true)
	fetchGoods = async (params?: iGoodsListOption) => api.goods.list(params)
	


	render() {

		const {
			user,
			isLoading,
			history,
			rubrics,
			achieves,
			purchases,
			meta
		} = this.state;

		if (!user || isLoading) return splashLoader;



		const modal =
			(<ModalRoot
				activeModal={this.state.activeModal}
				onClose={() => this.setState({ activeModal: null })}
			>

				<ModalCardComponent
					id='modal'
					onClose={() => this.setActiveModal(null)}
					modalData={this.state.modalData} />

				<ModalPage
					id='paid'
					onClose={() => this.setActiveModal(null)}
					header={
						<ModalPageHeader
							// left={<PanelHeaderButton onClick={() => this.setActiveModal(null)}>×</PanelHeaderButton>}
							right={<PanelHeaderButton onClick={() => this.setActiveModal(null)}>Закрыть</PanelHeaderButton>}
						></ModalPageHeader>
					}
					settlingHeight={100}
				>
					{this.state.modalData ? this.state.modalData.body : null}

				</ModalPage>
			</ModalRoot>)

		return (
			<View id="main" activePanel={this.state.activeView} modal={modal}>
				<Profile
					id="profile"
					snackbar={this.state.snackbar}
					rubrics={rubrics}
					go={this.go}
					user={user}
					history={history}
					achieves={achieves}
					openModal={(modal) => this.setActiveModal(modal)}
					events={this.state.events}
					getEvents={this.fetchLessons}
					api={api}
				/>
				<Rubric
					id='rubric'
					user={user}
					rubric={meta}
					go={this.go}
					rubricCellClickHandler={this.go}
					getLessons={this.fetchLessons}
				/>
				<LessonCard
					id="lesson"
					onBackClick={this.go}
					lessonID={meta.lessonID}
					lesson={meta.lesson}
					user={user}
					purchases={purchases}
					backTo={meta.backTo}
					openModal={(modal) => this.setActiveModal(modal)}
					api={api}
				/>
			</View >
		)
	}
}


export default App;

